@import url(https://fonts.googleapis.com/css2?family=Oleo+Script&family=Raleway&display=swap);
/** Fonts ********************************************** */
/* font-family: 'Oleo Script', cursive; */
/* font-family: 'Raleway', sans-serif; */

:root {
	--brown: #504841;
	--rust: #c05646;
	--blue-light: #b4cbcd;
	--white-orange: #ecebea;
	--white-red: #f5f5f5;
}

/** Reset ********************************************** */
* {
	padding: 0;
	margin: 0 0 10px 0;
	line-height: 1.2;
}

/** Elements ********************************************** */
html {
	font-family: "Raleway", sans-serif;
	font-size: 16px;
	color: #504841;
	color: var(--brown);
	background-color: #f5f5f5;
	background-color: var(--white-red);
}

body {
	max-width: 50rem;
	padding: 0 1rem 0 10rem;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h1 {
	font-family: "Oleo Script", cursive;
	font-size: 2.25rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.75rem;
}

ul {
	list-style: none;
}

li {
	padding: 0.5rem;
	margin-bottom: 0;
}

/** lvfha ********************************************** */
a:link,
a:visited {
	color: #504841;
	color: var(--brown);
	font-weight: bold;
	text-decoration: none;
}

a:focus,
a:hover {
	color: #c05646;
	color: var(--rust);
	/* text-decoration: underline; */
}

a:active {
	color: #b4cbcd;
	color: var(--blue-light);
	text-decoration: underline;
}

