button {
	font-size: 10px;
	color: var(--white-orange);
	background-color: var(--brown);
	border: 1px solid var(--brown);
	border-radius: 3px;
	padding: 5px 20px;
}

/** Large & Small *************** */
.button-large {
	padding: 7px 30px;
}

.button-small {
	padding: 2px 10px;
}

.button-outline {
	color: var(--brown);
	background-color: transparent;
}

.button-home {
	background-color: var(--rust);
	border-color: var(--rust);
	border-radius: 15px;
}

/** Hover *************** */
button:hover {
	-webkit-filter: brightness(0.95);
	        filter: brightness(0.95);
}

